            if (mar === -1) {
                sel = "select distinct mar.id, mar.nombre from modelo mar, producto_modelo pro, producto p where pro.modelo = mar.id and p.id = pro.producto and p.clasificacion1 = " + cla + " order by mar.nombre";
            }
