    DatabaseProvider.prototype.obtenerDetallesPedido = function (idPedido, porcimpuesto) {
        var _this = this;
        return new Promise(function (resolve, reject) {
            _this.database.executeSql("SELECT dpe.*, pro.nombre as nombreProducto, pro.impuesto, (SELECT uni.nombre FROM factor fac, umedida uni where uni.id = fac.umedida and fac.producto = pro.id) as uniNombre, " +
                " bod.nombre bodegaNombre " +
                " FROM dpedido dpe, producto pro, bodega bod where bod.id = dpe.bodega and pro.id = dpe.producto and cpedido = ?", [idPedido]).then(function (data) {
                var dpe = [];
                //console.log('data parametros: ',data)
                if (data.rows.length > 0) {
                    var total = 0.0;
                    var _loop_1 = function (i) {
                        var det = data.rows.item(i);
                        var pr = 0.0;
                        var iva = 0.0;
                        var prec = 0.0;
                        if (det.impuesto === 1) {
                            iva = (det.cantidad * det.precio) * (porcimpuesto / 100);
                            pr = (det.cantidad * det.precio) * (1 + porcimpuesto / 100);
                        }
                        console.log('Detalle que va a poner ', det);
                        dpe.push({
                            idProducto: det.producto,
                            cantidad: det.cantidad,
                            descuento: det.descuento,
                            nombreProducto: det.nombreProducto,
                            precio: det.precio,
                            promocion: 0.0,
                            cpedido: det.cpedido,
                            unidad: det.umedida,
                            bodega: det.bodega,
                            total: (det.cantidad * det.precio) + iva,
                            subtotal: det.cantidad * det.precio,
                            iva: iva,
                            nombreUnidad: det.uniNombre,
                            nombreBodega: det.bodegaNombre,
                            observaciones: det.observaciones
                        });
                        prec = (det.precio) * (1 + porcimpuesto / 100);
                        _this.insertarProductotmp({ id: det.producto, cantidad: det.cantidad, total: pr, preventa: det.preventa, precio: prec }).then(function (res) {
                            if (i === (data.rows.length - 1)) {
                                resolve(dpe);
                            }
                        });
                    };
                    for (var i = 0; i < data.rows.length; i++) {
                        _loop_1(i);
                    }
                }
            }, function (err) {
                console.log('Error: ', err);
                reject(err);
            });
        });
    };
